.field--name-field-skills .skills .skill {
  top: -3px;
  display: inline-block;
  background: #ddd;
  color: #222;
  font-size: 12px;
  height: 23px;
  line-height: 22px;
  padding: 0 14px 0 16px;
  position: relative;
  margin: 0 10px 0 0;
  text-decoration: none;
}
.field--name-field-skills .skills .skill::before {
  background: #FFFFFF;
  border-radius: 4px;
  box-shadow: inset 0 1px rgba(0, 0, 0, 0.25);
  content: "";
  height: 4px;
  left: 6px;
  position: absolute;
  width: 4px;
  top: 9px;
}
.field--name-field-skills .skills .skill::after {
  background: #FFFFFF;
  border-bottom: 11px solid transparent;
  border-left: 6px solid #ddd;
  border-top: 12px solid transparent;
  content: "";
  position: absolute;
  right: 0;
  top: 0;
}
.field--name-field-skills .skills .skill:hover {
  background-color: #666;
  color: #FFFFFF;
}
.field--name-field-skills .skills .skill:hover::after {
  border-left-color: #666;
}

.field--name-field-skills .skills {
  display: inline;
  list-style: none;
  padding: 0;
  margin: 0;
}
.field-name-field-project-url a {
  margin-top: 12px;
  display: inline-block;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  font-weight: 500;
  background-color: #81a5bf;
  padding: 15px 20px;
  color: #FFFFFF;
  text-decoration: none;
}
.field-name-field-project-url a:after {
  font-family: "FontAwesome";
  content: "\f35d";
  margin-left: 0.5rem;
}
.field-name-field-project-url a:hover, .field-name-field-project-url a:active {
  background-color: #5f8dae;
}
@media screen and (max-width: 768px) {
  .field-name-field-project-url a {
    display: block;
    text-align: center;
  }
}